import { Component, OnInit, Inject } from '@angular/core';
import { Router } from '@angular/router';

import { TbAuthService } from '../auth.service';

import { LoginRequest } from '../models/login-request';
import { StorageVars } from '../models/storage-vars';

@Component({
    selector: 'tb-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss']
})
export class TbLoginComponent implements OnInit {
    public capsLockOn = false;
    public loading = false;
    public loginRequest: LoginRequest;
    public redirectUrl: string;
    public logoB64 =
        'data:image/png;base64,';

    public subscriptionSelection: boolean; // abilita la scelta della subscription
    public loginSubscriptions: Array<{ description: string; subscriptionkey: string }> = [{ description: '', subscriptionkey: '' }];

    constructor(public authService: TbAuthService, public router: Router, @Inject('env') private env) {
        this.loginRequest = new LoginRequest();
        this.loginRequest.appId = env.auth.appid;
        this.subscriptionSelection = env.auth.subs ? env.auth.subs : false;
        this.redirectUrl = env.auth.redirectUrl ? env.auth.redirectUrl : '/';
    }

    ngOnInit() {
        this.loadAccountName();
    }

    // ---------------------------------------------------------------------------------------------
    keyUpFunction(event) {
        if (event.keyCode === 13) {
            if (this.loginRequest.accountName) this.login();
        }
        const capsOn = event.getModifierState && event.getModifierState('CapsLock');
        this.capsLockOn = capsOn;
    }

    // -------------------------------------------------------------------------------------
    disabledButton() {
        return !this.loginRequest.accountName || this.loading;
    }

    // -------------------------------------------------------------------------------------
    accountNameBlur() {}

    // ---------------------------------------------------------------------------------------------
    async login() {
        this.authService.errorMessage = '';
        this.saveAccountName();
        this.loading = true;

        const result = await this.authService.login(this.loginRequest).catch(err => {
            this.loading = false;
            this.authService.errorMessage = err.error && err.error.Message;
            return;
        });

        this.loading = false;

        if (!result) return;

        // todo controlla come vengono mostrati errori sia login sia checkdb
        if (result.Result) {
            this.authService.errorMessage = '';
            this.router.navigate([this.redirectUrl]);
        } else {
            this.loading = false;
        }
    }

    // -------------------------------------------------------------------------------------
    public loadAccountName() {
        this.loginRequest.accountName = localStorage.getItem(StorageVars.ACCOUNT_NAME);
    }

    // -------------------------------------------------------------------------------------
    public saveAccountName() {
        localStorage.setItem(StorageVars.ACCOUNT_NAME, this.loginRequest.accountName);
    }

    async getCompaniesForUser(user: string) {
        if (!user || !this.subscriptionSelection) {
            this.loginRequest.subscriptionKey = null;
            return;
        }

        this.authService.errorMessage = '';

        const temp = await this.requestAndSortSubscriptions(user);
        // Premio Eleganza Codice 2019 (@LucaBruni)
        if (JSON.stringify(temp) !== JSON.stringify(this.loginSubscriptions)) this.loginSubscriptions = temp;

        if (this.loginSubscriptions.length === 0) {
            this.loginRequest.subscriptionKey = null;
            localStorage.removeItem('_subscription');
            localStorage.removeItem('_company');
            this.authService.errorMessage = 'No Subscriptions associated to this user';
        }

        if (this.loginSubscriptions.length > 0) {
            if (!this.loginRequest.subscriptionKey) {
                this.loginRequest.subscriptionKey = this.loginSubscriptions[0]['subscriptionKey'];
            } else {
                if (
                    this.loginSubscriptions
                        .map(function(e) {
                            return e.subscriptionkey;
                        })
                        .indexOf(this.loginRequest.subscriptionKey) === -1
                ) {
                    this.loginRequest.subscriptionKey = this.loginSubscriptions[0]['subscriptionKey'];
                }
            }
        }
    }

    // ---------------------------------------------------------------------------------------------
    private async requestAndSortSubscriptions(user: string): Promise<Array<any>> {
        let temp = [];
        const result: Array<any> = await this.authService.getCompaniesForUser(user).toPromise();
        // console.log('result', result);
        result.sort(this.compareCompanies).forEach(c => {
            temp.push({ subscriptionkey: c.subscriptionkey, description: c.description });
        });
        return temp;
    }

    // ---------------------------------------------------------------------------------------------
    compareCompanies(c1, c2) {
        if (c1.description > c2.description) {
            return 1;
        } else if (c1.description < c2.description) {
            return -1;
        }
        return 0;
    }
}
